<template>
  <div>
    <Header></Header>
    <div class="box_evaluate ls1">
      <div class="head">
        <img :src="imgUrl+product.logo" alt="" :class="{creditImg:product.type='credit'}" v-if="product.type==='credit'">
        <img :src="imgUrl+product.logo" alt="" :class="{productImg:product.type='product'}" v-else>
        <p class="name" v-text="product.name">借了吗</p>
        <p class="satisfaction mb40">综合满意度(必选)</p>
        <p class="xin">
          <img v-for="(item, i) in star_" :key="i+'i'+item" src="../../images/ping-jia-xuan-zhong.png" @click="select_(i)" alt="">
          <img v-for="(item, i) in 5-star_" :key="i+item" src="../../images/ping-jia-wei-xuan.png" @click="select_(star_+i)" alt="">
        </p>
      </div>
      <div class="textarea">
        <textarea class="shadow bgfff pd40" @input="textNum_" v-model="textarea" maxlength="100" placeholder="请输入本次信用卡评价内容"  v-if="product.type==='credit'"></textarea>
        <textarea class="shadow bgfff pd40" @input="textNum_" v-model="textarea" maxlength="100" placeholder="请输入本次贷款评价内容" v-else></textarea>
        <p>{{textNum}}/100</p>
      </div>
      <div class="submit_">
        <p class= "mt40" @click="upDataEvaluate" :class="{ 'haveInput' :textarea.length}">提交评价</p>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
export default {
  components: {
    Header
  },
  data () {
    return {
      star_: 5, // 初始默认五星
      show: false,
      textNum: 0,
      textarea: '',
      product: JSON.parse(this.$route.params.item),
    }
  },
  created () {
    this.product.logo = this.product.logo.replace(/_\*_/g, '/')
  },
  methods: {
    // 整星
    select_ (i) {
      this.star_ = i + 1
    },
    textNum_ () {
      this.textNum = this.textarea.length
    },
    // 提交
    upDataEvaluate () {
       if (this.star_ <= 0) {
        this.$toast('请选择满意度', 2000, 1)
        return
      }
      if (!this.textarea) {
        this.$toast('请输入评价内容', 2000, 1)
        return
      }
      this.fetch.comment({
        'type': JSON.parse(this.$route.params.item).type, // 评论类型: 贷款=>product , 信用卡=>credit
        'id': this.product.product_id || this.product.credit_id, // type=product时, id=>产品id
        'apply_id': this.product.id, // 申请id
        // 'name': this.product.name,
        // 'logo': this.product.logo,
        'content': this.textarea,
        'star': this.star_ 
      },(res => {
        if (res.code === 200) {
          this.$toast(res.info, 2000, 2) // 信息提示：2表示成功
          this.$router.back(-1) // 评论成功后跳转
        } else if(res.code === 4001){
          this.$toast('登录过期，请重新登录', 2000, 1)
        }
        else {
          this.$toast(res.info, 2000, 1)
        }
      }))
    }
  }
}
</script>
<style lang="scss" scoped>

 // 输入激活样式
.submit_ .haveInput{
   opacity: 1!important;
  }
.box_evaluate{
  padding: .8rem .4rem;
  font-size: .26rem;
  .head{
    text-align: center;
    color: #101d37;
    .name{
      font-size: .26rem;
      letter-spacing: .01rem;
      color: #101d37;
      margin-top: .1rem;
    }
    .creditImg{
      width: 1.77rem;
      height: 1.1rem;
      border-radius: .1rem;
    }
    .productImg{
      width: .98rem;
      height: .98rem;
      border-radius: .2rem;
    }
    .satisfaction{
      font-size: .28rem;
      color: #a1a8b2;
      margin-top: .78rem;
    }
    .xin{
      display: flex;
      justify-content: center;
      img{
        width: .4rem;
        height: .4rem;
        margin: 0 .1rem;
      }
    }
  }
  .textarea{
    width: 6.7rem;
    height: 5.08rem;
    margin-top: .8rem;
    position: relative;
    font-size: .26rem;
    p{
      position: absolute;
      right: .4rem;
      bottom: .4rem;
      color: #c2c5cc;
    }
  }
  textarea{
    width: 6.7rem;
    height: 5.08rem;
    line-height: .4rem;
    border-radius: .1rem;
    box-sizing: border-box;
    color: #101d37;
    letter-spacing: .01rem;
  }
  .submit_{
     text-align: center;
    & p{
      width: 6.7rem;
      height: .9rem;
      line-height: .9rem;
      background-color: #2e6eec;
      opacity: 0.5;
      border-radius: .1rem;
      font-size: .3rem;
      color: #fff;
      }
    }
}

</style>



